#HomepageSkills {
  background-image: linear-gradient(
      to right bottom,
      rgba($black, 1),
      rgba($dark_green, 0.65)
    ),
    url("https://steven-jackson-dev.github.io/online-cv/static/media/fotis-fotopoulos-LJ9KY8pIH3E-unsplash.a3f7cf93.jpg");
  padding: 10vh;
  color: $white;
  transform: skewY(-4deg);

  @media screen and (max-width: 800px) {
    padding: 7vh;
  }

  & .card {
    @include transition();
    margin: 1rem 0;
    padding: 1rem;
    background-color: rgba($white, 0.1);
    border: 1px solid $white;
    &:hover {
      transform: translate(1.5rem, 1.5rem) skewY(4deg) scale(1.2);
      background-color: rgba($white, 1);
      color: $black;
    }
  }

  & .heading {
    transform: skewY(4deg);
  }

  & .links {
    transform: skewY(4deg);
    & a {
      background-color: $white;
      color: $black;
      box-shadow: none;

      &:hover {
        transform: translateX(10px);
      }
    }
  }
}
