#Experience {
    & p {
       font-size: 1em;
   }
    & h3 {
        border-bottom: 3px solid red;
        padding-bottom: 10px;
        width: 75%;
        margin: 0 auto;
        letter-spacing: 2px;
        text-transform: uppercase;
    }
    ul {
        list-style: none;
        @media screen and (max-width: 500px) {
            padding: 0 !important;
          }
          & li{
              padding: 10px 0;
          }
    }

    & .content {
        border-left: 1px solid #c3c3c3;
        padding: 0 2em;
        @media screen and (max-width: 500px) {
            text-align: center;
            padding-top: 2em;
          }
    }
}